import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../../icons/exports';

/**
 * @slot content - content of a single element
 * @slot button - button(s) of a single element
 */

@Component({
  tag: 'mdx-progressive-disclosure-item',
  styleUrl: 'progressive-disclosure-item.scss',
  shadow: true,
})
export class ProgressiveDisclosureItem {
  @Prop({ reflect: true })
  open = false;

  @Prop({ reflect: true })
  done = false;

  @Prop()
  step: number;

  @Prop()
  summary: string;

  @Prop()
  contentTitle: string;

  @Event()
  progressiveDisclosureClick: EventEmitter<{ open: boolean; step: number; done: boolean }>;

  render() {
    return (
      <details
        class="progressive-disclosure-item"
        open={this.open}
        aria-expanded={this.open}
        onClick={() => {
          this.open = !this.open;
          this.progressiveDisclosureClick.emit({ open: this.open, step: this.step, done: this.done });
        }}
      >
        <summary class="progressive-disclosure-item__summary-container">
          <div class="progressive-disclosure-item__summary">
            {this.done ? (
              <mdx-icon-check-circle class="progressive-disclosure-item__done-icon" size={ICON_SIZES.SM} />
            ) : (
              <div class="progressive-disclosure-item__todo-icon-container">
                <mdx-icon-circle class="progressive-disclosure-item__todo-icon" size={ICON_SIZES.SM} />
                <span class="progressive-disclosure-item__todo-step">{this.step}</span>
              </div>
            )}
            {this.summary}
          </div>
          {this.done && !this.open ? <mdx-icon-chevron-down class="progressive-disclosure-item__chevron" /> : null}
        </summary>
        <div class="progressive-disclosure-item__content-container">
          <div class="progressive-disclosure-item__content-title">{this.contentTitle}</div>
          <slot name="content" />
          <mdx-button-group>
            <slot name="button" />
          </mdx-button-group>
        </div>
      </details>
    );
  }
}
